import Vue from 'vue'
import App from './App.vue'
import router from './router'

//从vant组件库导入Button组件
import { 
	Row,
	Col,
	Tabbar,
	TabbarItem,
	Checkbox,
	CheckboxGroup,
	SubmitBar 
} from 'vant'

//根据屏幕适配rem
import 'lib-flexible/flexible'

//全局注册组件
Vue.use(Row)
Vue.use(Col)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Checkbox)
Vue.use(CheckboxGroup)
Vue.use(SubmitBar)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
