<template>
  <div class="home">
		<van-tabbar v-model="active">
			<van-tabbar-item v-for="(i , index) in btNav" :key="index" :icon="i.icon">{{i.title}}</van-tabbar-item>
		</van-tabbar>
		
		<div class="shop-box">
			<div class="tlt">购物车({{data.length}})</div>
			<div class="bianji" @click="bianji">编辑</div>
			<ul class="list">
				<li class="lis" v-for="(i , index) in data" :key="index">
					<div class="fl check-box"><van-checkbox v-model="i.checked" @change="changes(i)" checked-color="#F75C2C" icon-size="0.533rem"></van-checkbox></div>
					<div class="fl card-box">
						<span><img class="img-auto" :src="i.url" alt=""></span>
						<div class="txt-box fl">
							<div class="txt-tlt">{{i.name}}</div>
							<div class="pic-box" v-show="isTrue">
								<span class="txt-pic">¥{{i.price * i.num}}</span>
								<span class="txt-num">X{{i.num}}</span>
							</div>
							<div class="add-box" v-show="!isTrue">
								<button @click="jian(i)">-</button>
								<input type="text" v-model="i.num">
								<button @click="jia(i)">+</button>
							</div>
							<div class="del fr" @click="del(index)" v-show="!isTrue">x</div>
						</div>
					</div>
				</li>
			</ul>
			<van-submit-bar class="subs" :price="pic" :button-text="btntxt" @submit="cli" v-show="isAllDel">
				<van-checkbox class="all-box" checked-color="#F75C2C"  v-model="checked" @click="checkAlls">全选</van-checkbox>
				<button v-show="!isTrue" class="alldel" @click="allDel">全部删除</button>
			</van-submit-bar>
		</div>
  </div>
</template>

<style scoped lang="less">
	.home{
		background:#F4F4F4;
	}
	.subs{
		position: fixed;
		bottom: 1.333rem;
		left: 0;
	}
	.bianji{
		text-align: right;
		padding:.3rem .266rem;
		font-size: .426rem;
		position: sticky;
		top: 0;
	}
	.tlt{
		text-align: center;
		color: #F9F5D8;
		font-size: .533rem;
		padding: .266rem 0;
		background: #ED702E;
		position: sticky;
		top: 0;
	}
	.list{
		width: 96%;
		margin: 0 auto;
	}
	.lis{
		width: 100%;
		height: 4rem;
		background: #FAFAFA;
		border-radius: 15px;
		margin: .266rem 0;
	}
	.check-box{
		width: .533rem;
		padding: 1.746rem .266rem 0;
	}
	.card-box{
		width: calc(100% - .533rem - .266rem - .266rem);
		height: 95%;
		>span:first-child{
			display: block;
			float: left;
			width: 3rem;
			margin-top: 1.181rem;
		}
	}
	.txt-box{
		width: calc(100% - 3rem);
	}
	.txt-tlt{
		width: 90%;
		margin: .266rem auto;
		font-size: .426rem;
	}
	.pic-box{
		width: 90%;
		margin: .266rem auto;
	}
	.txt-pic{
		font-size: .533rem;
		color: #E39949;
		font-weight: bold;
	}
	.txt-num{
		margin-left: .4rem;
		font-size: .426rem;
	}
	.add-box{
		width: 90%;
		margin: .266rem auto;
		>button{
			width: 1rem;
			text-align: center;
			outline: none;
			background: none;
			border:1px solid #ddd
		}
		>input{
			outline: none;
			width: 1rem;
			text-align: center;
			border:1px solid #ddd
		}
	}
	.del{
		font-size: .533rem;
		color: #ddd;
		padding: .266rem;
	}
	.alldel{
		border: none;
		width: 110px;
		height: 50px;
		position: relative;
		left: 3rem;
		background: #fff;
	}
	.all-box{
		margin-left: .266rem;
	}
</style>

<script>

export default {
  name: 'home',
	created(){
		this.http().then(result => {
			result = JSON.parse(result)
			
			for(let i=0;i<result.length;i++){
				result[i]['checked'] = false;
				result[i]['num'] = 1;
			}
			this.data = result.concat();
		});
	},
	data(){
		return{
			checked:false,
			active:3,
			data:[],
			btntxt:'提交订单',
			isTrue:true,
			pic:0,
			isAllDel:true,
			btNav:[
				{
					name:'home',
					title:'首页',
					icon:'home-o'
				},
				{
					name:'classif',
					title:'分类',
					icon:'home-o'
				},
				{
					name:'jingxi',
					title:'京喜',
					icon:'home-o'
				},
				{
					name:'shopcart',
					title:'购物车',
					icon:'home-o'
				},
				{
					name:'mine',
					title:'我的',
					icon:'home-o'
				}
			]
		}
	},
	computed:{
// 		pic(){
// 			let sum=0;
// 			for(let i=0; i<this.data.length; i++){
// 				sum += this.data[i].price * this.data[i].num;
// 			}
// 			sum = parseFloat(sum+'00');
// 			return sum
// 		}
	},
	methods:{
		http(){
			return new Promise((resolve,reject) => {
				let xhr = new XMLHttpRequest();
				
				xhr.onreadystatechange=function(){
					if(this.readyState === 4 && this.status === 200){
						resolve(this.response);
					}
				}
				
				xhr.open('GET','http://api.kangliuyong.com:9002/pros?count=5',false);
				
				xhr.send(null)
				
			})
		},
		
		bianji(){
			if(this.isTrue){
				this.btntxt = '保存';
				this.isTrue=false;
			}else{
				this.btntxt = '提价订单';
				this.isTrue=true;
				this.money()
			}
		},
		
		changes(i){
			this.money();
			this.checks();
		},
		cli(){
			if(this.btntxt == '保存'){
				this.btntxt = '提价订单';
				this.isTrue = true;
			}
		},
		
		jia(i){
			i.num >=20 ? 20 : i.num++;
		},
		jian(i){
			i.num <=1 ? 1 : i.num--;
		},
		
		del(idx){
			
			this.data.splice(idx,1);
			if(this.data.length == 0){
				this.btntxt = '提价订单';
				this.isTrue=true;
				this.isAllDel = false
			}
		},
		
		checkAlls(){
			for(let i=0; i<this.data.length; i++){
			 	this.data[i].checked = !this.checked;
			}
		},
		
		
		money(){
			let sum=0;
			for(let i=0; i<this.data.length; i++){
				if(this.data[i].checked){
					sum += this.data[i].price * this.data[i].num;
				}
			}
			sum = sum * 100;
			
			this.pic = sum;
		},
		
		checks() {
			
			let flag = true;
			
			for(let j=0; j<this.data.length; j++){
			 	if(!this.data[j].checked){
					
					this.checked = false;
					return;
				}
			}
			this.checked = flag;
		},
		
		allDel(){
			this.data=[];
			this.btntxt = '提价订单';
			this.isTrue=true;
			this.money();
			this.isAllDel = false
		}
	}
}
</script>
